.table-header {
  color: $color-222;
  font-size: 11px;
  line-height: 20px;
  padding: 0 10px;

  div {
    background: $bg-lighter;
    box-shadow: -1px -1px $color-ddd inset;
  }
}

.table-row {
  color: $color-333;
  font-size: 12px;
  line-height: 23px;
  padding: 0 10px;

  &:nth-child(odd) {
    background: $bg-table-row;
  }

  &:active, &.active {
    background: $bg-table-row-highlight;
  }
}

.table-body {
  height: calc(100vh - 122px);
  overflow-x: hidden;
  overflow-y: auto;
  padding-bottom: 15px;
}

.table-loader {
  min-height: 100vh;
  padding-top: 30vh;
  text-align: center;
}
